import React, { Component } from "react"
import { Consumer } from "../GlobalComponents/logic/context"

export default class EditCaseButton extends Component {
  constructor(props) {
    super(props)
    this.state = {
      editCase: this.props.editCase,
      handleChange: this.props.handleChange
    }
    console.log(this.props)
    this.updateCase = this.updateCase.bind(this)
  }
  handleChange(e) {
    this.state.handleChange(e)
  }
  updateCase(e) {
    this.state.editCase()
    // dispatch({ type: "CREATE_CASE", payload: this.state })
  }

  render() {
    return (
      <Consumer>
        {value => {
          const { caseId, balance } = this.props.currentCase
          return (
            <div className={`row new-case`}>
              <div className="col">
                <div>
                  <i onClick={this.updateCase} className="fas fa-save" />
                </div>
                <form onSubmit={this.updateCase}>
                  <div className="form-group">
                    <label htmlFor="idNumberInput">ID Number</label>
                    <input
                      className="form-control"
                      id="idNumberInput"
                      name="caseId"
                      value={caseId.toUpperCase()}
                      onChange={e => this.handleChange(e)}
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="balanceInput">Balance</label>
                    <input
                      className="form-control"
                      id="balanceInput"
                      value={balance}
                      onChange={e => this.handleChange(e)}
                      name="balance"
                    />
                  </div>
                  <button
                    style={{ display: "none" }}
                    type="submit"
                    value="submit"
                  />
                </form>
              </div>
            </div>
          )
        }}
      </Consumer>
    )
  }
}
